.statistics {
  margin-bottom: 50px;
  width: 260px;
  padding-top: 10px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  background-color: #d1d1d1;
  border: 1px solid #8b8b8b;
  border-radius: 10px;
}

.title {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  color: #000000;
}

.notFound {
  color: #000000;
  padding-bottom: 10px;
}
